:root {
  --liner-grey: #121212linear-gradient( 180deg, rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16% );
  --orange: #f87719;
  --liner-orange: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --liner-orange-light: linear-gradient( 141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91% );
  --grey-white-theme: #595959;
  --secondary-black: #282828;
  --background: #111;
  --secondary-text: #f8f8f8;
  --grey: #b7b7b7;
  --white: #fff;
}

html, body {
  height: 100%;
}

body {
  font-family: Roboto, sans-serif;
}

.body__wrapper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
}

main {
