  justify-content: center;
  align-items: center;
  padding-bottom: 28px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  display: flex;
  position: relative;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

@media screen and (min-width: 768px) {
  .footer {
    flex-wrap: nowrap;
    justify-content: center;
    padding-bottom: 40px;
