}

@media screen and (max-width: 580px) {
  .footer__text {
    width: 236px;
  }
}

.footer__heart {
  align-self: baseline;
  position: relative;
}

.footer__heart:hover {
  fill: #ff001b;
}

.footer__heart-goit {
  width: 14px;
  height: 14px;
  width: 14px;
  fill: #ff6b08;
  animation: heartpulse 1.5s ease-in-out infinite;
}

.footer__link {
  cursor: pointer;
  text-decoration: underline;
}

.footer__link:focus, .footer__link:hover {
  color: #ff6b08;
}

.footer__modal {
  z-index: 2147483647;
  width: calc(100% - 40px);
  height: 100%;
  color: #ff6b08;
  text-shadow: 1px 1px #fff;
  text-align: center;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 10px;
  padding: 20px;
  font-size: 20px;
  font-weight: 500;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

@keyframes heartpulse {
  0% {
    fill: #ff6b08;
    transform: scale(1);
  }

  10% {
    fill: #ff001b;
    filter: drop-shadow(0 0 10px #ff6b08);
    transform: scale(1.4);
  }

  50% {
    fill: #ff6b08;
    transform: scale(1);
  }

  100% {
    fill: #ff001b;
    transform: scale(1.2);
  }
}

.modal__button-close {
  z-index: 1;
  width: 25px;
  height: 25px;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  outline: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 30px;
  right: 30px;
  box-shadow: -2px -2px 5px #fff, 3px 3px 5px rgba(0, 0, 0, .1);
}

.modal__button-close:hover {
  box-shadow: inset -1px -1px 3px #fff, inset 1px 1px 3px rgba(0, 0, 0, .1);
}

.modal__button-close:hover ~ .modal__cross {
  fill: #ff6b08;
  transform: scale(.8);
}

.modal__button-close:active ~ .modal__cross {
  fill: #ff6b08;
}

.modal__button-close:active {
  box-shadow: inset -1px -1px 3px #fff, inset 1px 1px 3px rgba(0, 0, 0, .1);
}

.modal__button-close:hover ~ .modal__cross {
  fill: #ff6b08;
}

.modal__icon-close {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-45%, -45%);
}

.modal__button-close:focus > .modal__icon-close, .modal__button-close:hover > .modal__icon-close {
  fill: #ff6b08;
  cursor: pointer;
}

